import './App.css';
import { Container, Nav, Navbar } from 'react-bootstrap';
import { createContext, lazy, Suspense, useEffect, useState } from 'react';
import data from './data';
import { Routes, Route, Link, useNavigate, Outlet, json } from 'react-router-dom';
import axios from 'axios';
import { useQuery } from 'react-query';

// import Detail from './routes/Detail';
// import Cart from './routes/Cart';

//     import ,     js 
//  Detai, Cart   ,   <Suspense> 
const Detail = lazy(() => import('./routes/Detail'));
const Cart = lazy(() => import('./routes/Cart'));

//context API    3 
export let Context1 = createContext();

function App() {

  useEffect(() => {
    if (!localStorage.getItem('watched')) {
      localStorage.setItem('watched', JSON.stringify([]));
    }
  }, [])

  // localStorage    array/object  
  //  JSON   ,
  //  JSON     JSON.parse   
  let obj = { name: 'kim' }
  localStorage.setItem('data', JSON.stringify(obj));
  let  = localStorage.getItem('data');

  let [shoes, setShoes] = useState(data);
  let [stock] = useState([10, 11, 12]); // context API  
  let navigate = useNavigate(); //    

  // axios.get('https://codingapple1.github.io/userdata.json').then((a) => {
  //   console.log(a.data)
  // })

  // react-query  ajax , return 2
  // 1. //   
  // 2.   (refetch) 
  // 3.  retry  
  // 4. state   
  // 5. ajax  
  let result = useQuery('', () => {
    return axios.get('https://codingapple1.github.io/userdata.json').then((response) => {
      console.log('')
      return response.data
    })
    // { staleTime : 2000 }
  });

  // result.data
  // result.isLoading
  // result.error

  return (
    <div className="App">
      <Navbar bg="dark" variant="dark">
        <Container>
          <Navbar.Brand onClick={() => { navigate('/'); }}>Shoe Shop</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link onClick={() => { navigate('/'); }}>Home</Nav.Link>
            <Nav.Link onClick={() => { navigate('/detail'); }}>Detail</Nav.Link>
            <Nav.Link onClick={() => { navigate('/cart'); }}>Cart</Nav.Link>
          </Nav>
          <Nav className="ms-auto" style={{ color: 'white' }}>
            {/* { result.isLoading ? '' : result.data.name} */}
            {result.isLoading && ''}
            {result.error && ''}
            {result.data && result.data.name}
          </Nav>
        </Container>
      </Navbar>

      {/* <Link to="/"></Link>
      <Link to="/detail"> </Link> */}
      <Suspense fallback={<div></div>}>
        <Routes>
          <Route path='/' element={<Main shoes={shoes} setShoes={setShoes} />} />
          {/* :id = url,   element   */}
          <Route path='/detail/:id' element={

            // context API   , value  state 
            <Context1.Provider value={{ stock }}>
              <Detail shoes={shoes} />
            </Context1.Provider>

          } />
          <Route path='/cart' element={<Cart />} />
          {/* nested routes */}
          <Route path='/about' element={<About />}>
            <Route path='member' element={<div></div>} />
            <Route path='location' element={<div></div>} />
          </Route>
          <Route path='/event' element={<Event />}>
            <Route path='one' element={<div>   </div>} />
            <Route path='two' element={<div>  </div>} />
          </Route>
          {/* path='*'    (404) */}
          <Route path='*' element={<div>404</div>} />
        </Routes>
      </Suspense>

    </div>
  );
}

function Item(props) {

  return (
    <div className='col-md-4'>
      <Link to={'/detail/' + (props.i)} style={{ textDecoration: 'none', color: 'black' }}>
        <img src={'https://codingapple1.github.io/shop/shoes' + (props.i + 1) + '.jpg'} width="80%" />
        <h4>{props.shoes.title}</h4>
        <p>{props.shoes.content}</p>
        <p>{props.shoes.price}</p>
      </Link>
    </div>
  )

}

